.team-detail-page .currency-list {
  margin-top: 0.9375rem;
}
.team-detail-page .currency-list .list-top {
  height: 2.5rem;
  line-height: 2.5rem;
  font-family: 0.75rem;
  color: #ffffff;
  border-radius: 10px 10px 0px 0px;
}
.team-detail-page .currency-list .list-item:nth-child(2n-1) {
  background: #1f2027;
}
.team-detail-page .currency-list .list-item:nth-child(2n) {
  background: #16181e;
}
.team-detail-page .currency-list .list-item {
  display: flex;
  flex: 4 1;
  text-align: center;
  height: 2.5rem;
  line-height: 2.5rem;
  font-family: 0.75rem;
  color: #ffffff;
}
.team-detail-page .currency-list .list-item div {
  flex: 1 1;
}
.team-detail-page .title {
  font-size: 1.125rem;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: #ffffff;
  padding-top: 0.9375rem;
  padding-left: 0.9375rem;
}
.team-detail-page .time {
  font-size: 0.75rem;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: #ffffff;
  padding-top: 0.53125rem;
  padding-left: 0.9375rem;
}
.team-detail-page .banner {
  display: flex;
  margin-left: 0.9375rem;
  margin-right: 0.9375rem;
  margin-top: 0.9375rem;
  height: 7.5rem;
  border-radius: 0.3125rem;
  background: linear-gradient(90deg, #0070ff, #00bfff);
  justify-content: center;
  align-items: center;
}
.team-detail-page .banner p {
  width: 100%;
  text-align: center;
}
.team-detail-page .banner .banner-top {
  font-size: 0.75rem;
  color: #FFFFFF;
  font-weight: 400;
  opacity: 0.5;
}
.team-detail-page .banner .banner-bottom {
  font-size: 2.5rem;
  color: #FFFFFF;
  font-weight: bold;
}
.team-detail-page .detail-contain {
  display: flex;
  flex: 3 1;
  align-items: center;
  margin-left: 0.9375rem;
  margin-right: 0.9375rem;
  margin-top: 0.9375rem;
  padding-top: 0.9375rem;
  padding-bottom: 0.9375rem;
  height: 3.75rem;
  border-radius: 0.3125rem;
  background: linear-gradient(-45deg, #2b2d37, #282b39);
}
.team-detail-page .detail-contain .item {
  flex: 1 1;
  justify-content: center;
  padding: 0.9375rem;
  padding-top: 0.3125rem;
  padding-bottom: 0.3125rem;
  text-align: center;
}
.team-detail-page .detail-contain .item .item-top {
  font-size: 0.9375rem;
  font-weight: 400;
  color: #00d2ff;
}
.team-detail-page .detail-contain .item .item-top .tip {
  font-size: 0.625rem;
}
.team-detail-page .detail-contain .item .item-bottom {
  font-size: 0.625rem;
  font-weight: 400;
  color: #999999;
}
.team-detail-page .detail-contain .item-bottom-line {
  position: relative;
}
.team-detail-page .detail-contain .item-bottom-line:after {
  content: " ";
  position: absolute;
  right: 0;
  top: 0;
  width: 1px;
  bottom: 0;
  border-right: 1px solid #999999;
  color: #999999;
  transform-origin: 100% 0;
  transform: scaleX(0.5);
}
.team-detail-page .button {
  margin-top: 0.9375rem;
  margin-bottom: 0.625rem;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}
.team-detail-page .button .customButton1 .am-button {
  background: linear-gradient(90deg, #999999, #666666);
}
.team-detail-page .history-list {
  margin-top: 0.9375rem;
}
.team-detail-page .history-list .list-top p {
  font-size: 0.75rem;
}
.team-detail-page .history-list .list-item:nth-child(2n-1) {
  background: #1f2027;
}
.team-detail-page .history-list .list-item:nth-child(2n) {
  background: #16181e;
}
.team-detail-page .history-list .list-item {
  display: flex;
  flex: 2 1;
  align-items: center;
  margin-left: 0.9375rem;
  margin-right: 0.9375rem;
  height: 2.5rem;
}
.team-detail-page .history-list .list-item p {
  flex: 1 1;
  text-align: center;
  font-size: 0.625rem;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: #ffffff;
}
.redeem-modal .title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 2.5rem;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
  position: relative;
}
.redeem-modal .title:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #666666;
  color: #666666;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.redeem-modal .title .right {
  font-size: 0.875rem;
  color: #ffffff;
  font-weight: 400;
}
.redeem-modal .title .left {
  font-size: 0.5625rem;
  color: #ffffff;
  font-weight: 400;
}
.redeem-modal .price-detail {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.6875rem;
  height: 1.875rem;
  color: #999999;
}
.redeem-modal .price-detail .red {
  color: #FF3333;
}
.redeem-modal .offer-price-input {
  margin-top: 0.8125rem;
  background: rgba(255, 255, 255, 0);
}
.redeem-modal .offer-price-input .password .am-input-extra {
  color: #00D2FF !important;
}
.redeem-modal .offer-price-input .password .am-list-line {
  position: relative;
}
.redeem-modal .offer-price-input .password .am-list-line:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #fff;
  color: #fff;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.redeem-modal .offer-price-input .am-list-item {
  background: rgba(255, 255, 255, 0);
  padding-left: 1.5625rem;
  padding-right: 1.5625rem;
}
.redeem-modal .offer-price-input .am-list-item .am-list-line {
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}
.redeem-modal .offer-price-input .am-list-item .am-list-line input {
  font-size: 0.75rem;
  font-weight: 400;
  color: #666666;
}
.redeem-modal .offer-price-input .am-list-item .am-list-line .am-input-extra {
  color: #ffffff;
}
.redeem-modal .des-tip {
  display: flex;
  justify-content: flex-end;
  padding-right: 1.5625rem;
  font-size: 0.5625rem;
  color: #00D2FF;
}
.redeem-modal .des-tip .red {
  color: #FF3333 !important;
}
.redeem-modal .footerButton {
  margin-top: 2.5rem;
  margin-bottom: 3.125rem;
  padding-left: 1.5625rem;
  padding-right: 1.5625rem;
}
